import React, { useState } from 'react';
import connect from '@vkontakte/vk-connect';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import {ModalRoot, ModalPage, ModalPageHeader, FormLayout, SelectMimicry, FormLayoutGroup, Radio} from '@vkontakte/vkui';

	const directionList = ['Программная инженерия', 'Бизнес-информатика'];
	const coursesList = ['1', '2', '3', '4', 'Магистратура-1', 'Магистратура-2'];
	const groupsList = ['ПрИ-101', 'ПрИ-102', 'ПрИ-201', 'БИ-101'];
	const modalHistory = new Array();


	const ModalGroupSelector = ({id, go, open}) => {
	const [directionValue, setDirectionValue] = useState(null);
	const [courseValue, setCourseValue] = useState(null);
	const [groupValue, setGroupValue] = useState(null);
	const [modal, updateModal] = useState(null);

	function setOneModal(value) {
		modalHistory.push(value);
		updateModal(value);
	}
	function setModalBack() {
		modalHistory.pop();
		updateModal(modalHistory[modalHistory.length - 1]);
	}
	if(open != null && modal == null) setOneModal(open); //Init. Doesn't work without it

	return(
		<ModalRoot id={id} activeModal={modal}>
		<ModalPage
			id="groupSelectorPage"
			header={
				<ModalPageHeader>
					Давайте всё настроим :)
				</ModalPageHeader>
			}
			onClose={
				() => setModalBack()
			}>
			<FormLayout>
				<SelectMimicry top="Направление" placeholder="Выбрать направление" onClick={() => setOneModal('direction')}>{directionValue}</SelectMimicry>
				<SelectMimicry top="Курс" placeholder="Выбрать курс" onClick={() => setOneModal('course')}>{courseValue}</SelectMimicry>
				<SelectMimicry top="Группа" placeholder="Выбрать группу" onClick={() => setOneModal('group')}>{groupValue}</SelectMimicry>
				<Button 
					size="xl" 
					onClick={() => { setModalBack(); go("persik"); }} 
					>Погнали</Button>
			</FormLayout>
		</ModalPage>

		<ModalPage
			id="direction"
			header={
				<ModalPageHeader>
					Выберите направление
				</ModalPageHeader> //direction
			}
			onClose={
				() => setModalBack()
			}
		>
			<FormLayoutGroup>
				{
					directionList.map(
						(title, id) => {
							return (
								<Radio key={id} name="direction" value={id} onClick={()=> {setDirectionValue(title); setModalBack();}}>{title}</Radio>
							);
						}
					)
				}
			</FormLayoutGroup>
		</ModalPage>

		<ModalPage
			id="course"
			header={
				<ModalPageHeader>
					Выберите курс
				</ModalPageHeader> //course
			}
			onClose={
				() => setModalBack()
			}
		>
			<FormLayoutGroup>
				{
					coursesList.map(
						(title, id) => {
							return (
								<Radio key={id} name="direction" value={id} onClick={()=> { setCourseValue(title); setModalBack();}}>{title}</Radio>
							);
						}
					)
				}
			</FormLayoutGroup>
		</ModalPage>

		<ModalPage
			id="group"
			header={
				<ModalPageHeader>
					Выберите группу
				</ModalPageHeader> //group
			}
			onClose={
				() => setModalBack()
			}
		>
			<FormLayoutGroup>
				{
					groupsList.map(
						(title, id) => {
							return (
								<Radio key={id} name="direction" value={id} onClick={()=> { setGroupValue(title); setModalBack();}}>{title}</Radio>
							);
						}
					)
				}
			</FormLayoutGroup>
		</ModalPage>

	</ModalRoot>
);
}
export default ModalGroupSelector;